<template>
  <div>
    <cms-breadcrumb to="/messageSet/captcha/list">
      <li>验证码信息-新增</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt :collapse="false">
        <li>设置系统对商家发送消息的内容、方式</li>
      </cms-prompt>
      <el-form ref="form" :rules="saveRules" :model="formData" label-width="210px">
        <el-form-item label="模板标题" prop="mesTitle" :error="errorMessage.mesTitle">
          <el-input v-model="formData.mesTitle" :autofocus="true" ref="first" maxlength="100"></el-input>
        </el-form-item>
        <el-form-item label="模板标识" prop="mesCode" :error="errorMessage.mesCode">
          <el-input v-model="formData.mesCode" maxlength="100"></el-input>
          <div class="gray">建议使用英文标识，全局唯一</div>
        </el-form-item>
        <el-form-item label="模板描述" prop="remark" :error="errorMessage.remark">
          <el-input v-model="formData.remark" type="textarea" :rows="6" maxlength="200"></el-input>
        </el-form-item>
        <el-tabs v-model="activeName" @tab-click="changeTabs">
          <el-tab-pane label="邮件" name="1">
              <el-form-item label="是否开启邮件">
                <cms-switch v-model="formData.details[0].isOpen" @change="changeOpen"></cms-switch>
              </el-form-item>
              <el-form-item label="消息标题" prop="details[0].mesTitle"
               :rules="formData.details[0].isOpen? [{ required: true, message: '消息标题必须填写', trigger: 'blur' }] :[] ">
                <el-input v-model="formData.details[0].mesTitle" maxlength="100"></el-input>
              </el-form-item>
              <el-form-item label="模板内容" prop="details[0].mesContent"
              :rules="formData.details[0].isOpen? [{ required: true, message: '模板内容必须填写', trigger: 'blur' }] :[] ">
                <el-input
                  type="textarea"
                  :rows="4"
                  v-model="formData.details[0].mesContent"
                  maxlength="200"
                ></el-input>
              </el-form-item>
              <el-form-item label="提示文案" prop="details[0].extendedField">
                <el-input v-model="formData.details[0].extendedField" maxlength="200"></el-input>
              </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="短信" name="2">     
              <el-form-item label="是否开启手机">
                <cms-switch v-model="formData.details[1].isOpen" @change="changeOpen"></cms-switch>
              </el-form-item>
              <el-form-item label="模板ID" prop="details[1].tplId"
               :rules="formData.details[1].isOpen? [{ required: true, message: '模板id必须填写', trigger: 'blur' }] :[] ">
                <el-input v-model="formData.details[1].tplId" maxlength="100"></el-input>
              </el-form-item>
              <el-form-item label="消息内容" prop="details[1].mesContent"
               :rules="formData.details[1].isOpen? [{ required: true, message: '模板内容必须填写', trigger: 'blur' }] :[] ">
                <el-input
                  type="textarea"
                  :rows="4"
                  v-model="formData.details[1].mesContent"
                  maxlength="200"
                ></el-input>
              </el-form-item> 
              <el-form-item label="提示文案" prop="details[1].extendedField">
                <el-input v-model="formData.details[1].extendedField" maxlength="200"></el-input>
              </el-form-item>    
          </el-tab-pane>
        </el-tabs>
        <div class="cms-form-footer">
          <cms-button @click="saveData(false)" v-color>保存并新增</cms-button>
          <cms-button @click="saveData(true)" v-color>保存</cms-button>
          <cms-button type="rest" @click="resetAll">重置</cms-button>
        </div>
      </el-form>
    </div>
  </div>
</template>

<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import { messageTpSave, checkMesCode } from '@/api/setting/messageSet.js'
var defaultData = {
  mesCode: '',
  mesTitle: '',
  remark: '',
  details: [
    {
      isOpen: false,
      mesType: '2',
      mesContent: '',
      mesTitle: '',
      extendedField: ''
    },
    {
      isOpen: false,
      mesType: '3',
      mesContent: '',
      tplId: '',
      extendedField: ''
    }
  ]
}
export default {
  name: 'messageSetShopSave',
  mixins: [form],
  data() {
    var validateCode = (rule, value, callback) => { // 验证模板标识的唯一性
      checkMesCode({
        mesCode: this.formData.mesCode
      }).then(res => {
        if (res.code === 200) {
          callback()
        } else {
          callback(new Error('模板标识已存在，不允许重复绑定'))
        }
      })
    }
    return {
      activeName: '1',
      formData: Object.assign({}, defaultData),
      saveRules: { // 新增规则
        mesCode: [
          rules.required('请填写模板标识'),
          rules.english(),
          rules.len(1, 150),
          rules.custom(validateCode)
        ],
        mesTitle: [rules.required('请填写模板标题'), rules.len(1, 150)],
        remark: [rules.len(1, 500)]
      }
    }
  },
  methods: {
    saveData(state) {
      this.formData.mesType = this.$route.query.mesType
      this.$refs['form'].validate((valid, failObj) => { // 验证表格
        const arr = Object.keys(failObj)// 获取验证不通过的项的 键所组成的数组
        let target = ''// 存储tabs栏下验证未通过的项
        const reg = /\d/
        if (arr.length > 0) { // 当有未通过的项时
          arr.forEach(v => {
            target = v.match(reg)// 匹配出第一项含有数字的数组
          })
        }
        if (target) { // 切换未填写的tabs
          this.activeName = String(parseInt(target[0]) + 1)// 处理到需要的字符串
        }
        if (valid) {
          messageTpSave(this.formData).then(res => {
            if (res.code === 200) {
              this.$successMessage('保存成功')
              if (state) { // state为真。则跳转
                this.$routerLink('/messageSet/captcha/list', 'list')
                this.resetAll()
              } else {
                this.resetAll()
              }
            } else {
              this.$errorMessage(res.message)
            }
          })
        } else {
          return false
        }
      })
    },
    changeOpen(status) {
      if (!status) {
        this.$refs.form.clearValidate()
      }
    },
    resetAll() { // 清空所有的列表所有的数据
      this.formData = Object.assign({}, defaultData)
      this.formData.details.forEach(item => {
        for (const key in item) {
          // 循环去清空
          item[key] = ''
          item.isOpen = false
        }
      })
      this.$refs.form.resetFields()
      this.formData.details[0].mesType = '2' // 必要的信息还是需要填回去
      this.formData.details[1].mesType = '3'
    },
    changeTabs(tab, event) {
      this.activeName = tab.name
    }
  }
}
</script>

<style>
</style>



